import {
  ADD_BUN,
  ADD_INGREDIENT,
  DELETE_INGREDIENT,
  CHANGE_INDEXES,
} from "../actions/burger-constructor";

const initialState = {
  bun: {},
  topping: [],
  totalPrice: 0,
};

const burgerConstructorReducer = (state = initialState, action) => {
  switch (action.type) {
    case ADD_BUN:
      return {
        ...state,
        bun: { ...action.bun },
      };
    case ADD_INGREDIENT:
      return {
        ...state,
        topping: [...(state.topping || []), action.payload],
      };
    case DELETE_INGREDIENT:
      return {
        ...state,
        topping: state.topping.filter(
          (item) => item.uniqueId !== action.uniqueId
        ),
      };
    case CHANGE_INDEXES: {
      const newTopping = [ ...state.topping ]
      const swapItems = {
        from: newTopping[action.from],
        to: newTopping[action.to],
       }
       newTopping[action.from] = swapItems.to;
       newTopping[action.to] = swapItems.from;
      return {
        ...state,
        topping: newTopping
      };
    }
    default:
      return state;
  }
};

export default burgerConstructorReducer;
